const user = {
  namespaced: true,
  state: {
    basePath: "",
    groupName: "",
    defines: {},
    moduleList: [],
  },
  mutations: {
    SET_BASE_PATH: (state, v) => {
      state.basePath = v;
    },
    SET_GROUP_NAME: (state, v) => {
      state.groupName = v;
    },
    SET_DEFINES: (state, v) => {
      state.defines = v;
    },
    SET_MODULE_LIST: (state, v) => {
      state.moduleList = v;
    },
  },
  actions: {
    SET_BASE_PATH: (context, basePath) => {
      context.commit('SET_BASE_PATH', basePath);
    },
    SET_GROUP_NAME: (context, v) => {
      context.commit('SET_GROUP_NAME', v);
    },
    SET_DEFINES: (context, v) => {
      context.commit('SET_DEFINES', v);
    },
    SET_MODULE_LIST: (context, v) => {
      context.commit('SET_MODULE_LIST', v);
    },
  },
};

export default user;



