<template>
  <el-container style="height: 100%;">
    <el-header>接口文档</el-header>
    <el-container>
      <el-aside ><smenu></smenu></el-aside>
      <el-container>
        <el-main style="padding:5px;"> <router-view/></el-main>
        <el-footer class="c-footer">JAPI-UI 重庆品目网络科技</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>

import {mapActions} from 'vuex';
import smenu from "./menu";
export default {
  name: 'index',
  components: {smenu},
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
    };
  },
  created () {
    this.loadData();
  },
  methods: {
    ...mapActions('menu', ['SET_BASE_PATH', 'SET_GROUP_NAME', 'SET_DEFINES', 'SET_MODULE_LIST']),
    loadData () {
      var that = this;
      that.$http.get('/japi/docs-json', {}).then((res) => {
        that.SET_BASE_PATH(res.data.basePath);
        that.SET_GROUP_NAME(res.data.groupName);
        that.SET_DEFINES(res.data.defines);
        that.SET_MODULE_LIST(res.data.moduleList);
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-header{
    background-color:#4887bd;
    text-align: center;
    line-height: 60px;
    color: white;
  }
   .c-footer {
     border-top:1px solid lightgray;
     line-height: 60px;
     text-align: center;
  }

  .el-aside {
    background-color: #337ab7;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
    font-size: 14px;
  }

</style>



